@import url("https://fonts.googleapis.com/css2?family=Monomaniac+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bungee+Inline&display=swap");
.App {
  color: white;
  text-align: center;
}
.scores {
  font-size: 0.9em;
  display: flex;
  justify-content: space-evenly;
}
h1,
h2,
h3 {
  font-family: "Bungee Inline", cursive;
  margin: 0;
}
h3 {
  font-size: 1.3rem;
  font-weight: lighter;
}
h1 {
  font-size: 2.3rem;
  word-spacing: 3px;
  letter-spacing: 3px;
  color: rgb(179, 1, 1);
}

.selectDifficulty {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  animation: myAnim 2s ease-out 0s 1 normal forwards;
}
.selectDifficulty > div {
  width: 40vw;
  border-block: 3px double white;
  padding: 50px 30px;
  background: radial-gradient(
    circle,
    rgba(0, 0, 0, 0.9780287114845938) 0%,
    rgba(4, 4, 36, 0.6194852941176471) 50%,
    rgba(20, 1, 1, 0.651) 100%
  );
}

.cardGrid {
  display: grid;
  gap: max(2rem, 2vw);
  place-content: center;
  padding: 3rem;
  grid-template-columns: repeat(4, minmax(19vw, 200px));
}
.cardContainer {
  min-height: 210px;
  max-height: 260px;
  position: relative;
  background-color: rgba(0, 0, 0, 0.233);
  border: 3px solid;
  border-image: linear-gradient(
    230deg,
    rgba(250, 0, 0, 0.486) 0%,
    rgba(23, 23, 23, 0) 50%,
    rgba(255, 255, 255, 0.712) 100%
  );
  border-image-slice: 1;
}
.cardContainer::before {
  content: "";
  background: linear-gradient(
    140deg,
    rgba(0, 0, 0, 0.425) 0%,
    rgba(255, 255, 255, 0.219) 17%,
    rgba(0, 0, 0, 0.425) 32%,
    rgba(255, 255, 255, 0.219) 50%,
    rgba(0, 0, 0, 0.425) 75%,
    rgba(255, 255, 255, 0.219) 94%
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 600%;
  z-index: -1;
  width: calc(100% + 6px);
  height: calc(100% + 6px);
  filter: blur(8px);
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
  opacity: 1;
}
.cardContainer:hover {
  transition: transform 0.2s ease;
  transform: scale(1.05);
}
.cardContainer:hover::before {
  transform: scale(1.03);
  animation: glowing 20s linear infinite;
}

.cardContainer > div {
  display: flex;
  justify-content: center;
  width: 100%;
}
h4 {
  font-family: "Courier New", Courier, monospace;
  margin: 0;
  width: 100%;
  bottom: 0;
  filter: blur();
  background: linear-gradient(
    180.3deg,
    rgba(221, 221, 221, 0.158) 5.5%,
    rgba(44, 54, 65, 0.486) 90.2%
  );

  position: absolute;
}
h4::first-letter {
  text-transform: capitalize;
}
img {
  width: 100%;
  max-height: 100%;
  min-height: 75%;
}
.green {
  color: green;
}
.red {
  color: red;
}
dialog {
  overflow: hidden;
  padding: 30px;
  background-color: rgba(0, 0, 0, 0.589);
  position: fixed;
  inset: 0;
  margin: auto;
}
dialog > div {
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 700% 0;
  }
  100% {
    background-position: 0 0;
  }
}
@keyframes myAnim {
  0% {
    opacity: 0;
    transform: rotateX(-100deg);
    transform-origin: top;
  }

  100% {
    opacity: 1;
    transform: rotateX(0deg);
    transform-origin: top;
  }
}
@media screen and (max-width: 600px) {
  .cardGrid {
    grid-template-columns: repeat(2, 1fr);
  }
  .cardContainer {
    min-height: 120px;
    max-height: 150px;
  }
  dialog {
    overflow: hidden;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.589);
    position: fixed;
    inset: 0;
    margin: auto;
  }
  dialog div {
    flex-direction: column;
  }
  .selectDifficulty > div {
    width: inherit;
  }
}
