* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
dialog > div > button {
  --clr: rgb(255, 255, 255);
}
#easy {
  --clr: rgb(65, 200, 224);
}
#medium {
  --clr: rgb(250, 52, 223);
}
#hard {
  --clr: rgb(253, 205, 47);
}

.difficultyButtons,
dialog > div {
  margin-top: 5%;
  display: flex;
  background: #00000000;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  justify-content: center;
}
dialog > div {
  flex-direction: row;
}
button {
  position: relative;
  background: #444444;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  border: none;
  letter-spacing: 0.2rem;
  font-size: 1rem;
  padding: 1rem 2rem;
  transition: 0.2s;
}
button:hover {
  letter-spacing: 0.2rem;
  padding: 1.2rem 2.7rem;
  background: var(--clrm, currentColor);
  color: var(--clr, currentColor);
  box-shadow: 0 0 35px var(--clr);
  animation: box 3s infinite;
}

button::before {
  content: "";
  position: absolute;
  inset: 2px;
  background: #272822d8;
}

button span {
  position: relative;
  z-index: 1;
}
button i {
  position: absolute;
  inset: 0;
  display: block;
}

button i::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 2px;
  left: 80%;
  top: -2px;
  border: 2px solid var(--clr, currentColor);
  background: #272822;
  transition: 0.2s;
}

button:hover i::before {
  width: 15px;
  left: 20%;
  animation: move 3s infinite;
}

button i::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 2px;
  left: 20%;
  bottom: -2px;
  border: 2px solid var(--clr, currentColor);
  background: #272822;
  transition: 0.2s;
}

button:hover i::after {
  width: 15px;
  left: 80%;
  animation: move 3s infinite;
}

@keyframes move {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(5px);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes box {
  0% {
    box-shadow: #27272c;
  }
  50% {
    box-shadow: 0 0 25px var(--clr, currentColor);
  }
  100% {
    box-shadow: #27272c;
  }
}
